import React, { useState } from "react";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBCard,
  MDBCardBody,
} from "mdb-react-ui-kit";
import {
  FormControl,
  FormControlLabel,
  FormLabel,
  InputLabel,
  Select,
  Radio,
  MenuItem,
  RadioGroup,
  TextField,
} from "@mui/material";
import { AdapterDayjs } from "@mui/x-date-pickers/AdapterDayjs";
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
import { DatePicker } from "@mui/x-date-pickers/DatePicker";

function DemographicInformation() {
  const [ethenicOrigin, setethenicOrigin] = React.useState("");
  const [dob, setDob] = React.useState(null);

  const handleEthincOrigin = (event) => {
    setethenicOrigin(event.target.value);
  };

  const handleResidencyStatus = (event) => {
    if (event.target.value === 2) {
      document.getElementById("citenship_status").style.display = "block";
    } else {
      document.getElementById("citenship_status").style.display = "none";
    }
  };

  return (
    <div>
      <MDBCard>
        <MDBCardBody className="px-4">
          <h3 className="fw-bold mb-2 pb-2 pb-md-0 mb-md-3">
            Demographic Information
          </h3>
          <MDBRow>
            <MDBCol md="6">
              <TextField
                id="student-first-name"
                label="Student's First Name"
                variant="outlined"
                fullWidth
                sx={{ mb: 2 }}
              />
            </MDBCol>
            <br/>
            <MDBCol md="6">
              <TextField
                id="student-last-name"
                label="Student's Last Name"
                variant="outlined"
                fullWidth
                sx={{ mb: 2 }}
              />
            </MDBCol>
          </MDBRow>
          <MDBRow>
            <MDBCol md="12">
              <LocalizationProvider dateAdapter={AdapterDayjs}>
                <DatePicker
                  label="Date of Birth"
                  value={dob}
                  onChange={(newValue) => {
                    setDob(newValue);
                  }}
                  renderInput={(params) => <TextField {...params} 
                  fullWidth
                  sx={{ mb: 2 }}
                  />}
                />
              </LocalizationProvider>
            </MDBCol>
          </MDBRow>
          <MDBRow>
            <MDBCol md="5">
              <FormControl fullWidth>
                <InputLabel id="residency-status-select-label">
                  Residency Status :{" "}
                </InputLabel>
                <Select
                  labelId="residency-status-select-label"
                  id="resideny-status-select"
                  label="residency Status"
                  onChange={handleResidencyStatus}
                  sx={{ mb: 2 }}
                >
                  <MenuItem value={1}>
                    US Citizen or Permanent Resident
                  </MenuItem>
                  <MenuItem value={2}>International</MenuItem>
                </Select>
              </FormControl>
            </MDBCol>
            <MDBCol md="7" style={{ display: "none" }} id="citenship_status">
              <TextField
                id="residential-status-non-us-text-field"
                label="Citizenship Status"
                variant="standard"
                fullWidth
                sx={{ mb: 2 }}
              />
            </MDBCol>
          </MDBRow>
          <MDBRow>
            <MDBCol md="12">
              <TextField
                id="student-address-line1"
                label="Street Address"
                variant="outlined"
                fullWidth
                sx={{ mb: 2 }}
              />
            </MDBCol>
          </MDBRow>
          <MDBRow>
            <MDBCol md="4">
              <TextField
                id="student-address-line1"
                label="City"
                variant="outlined"
                fullWidth
                sx={{ mb: 2 }}
              />
            </MDBCol>
            <MDBCol md="4">
              <TextField
                id="student-address-line1"
                label="State"
                variant="outlined"
                fullWidth
                sx={{ mb: 2 }}
              />
            </MDBCol>
            <MDBCol md="4">
              <TextField
                id="student-address-line1"
                label="ZIP Code"
                variant="outlined"
                fullWidth
                sx={{ mb: 2 }}
              />
            </MDBCol>
          </MDBRow>
          <MDBRow>
            <MDBCol md='12'>
            <FormControl fullWidth>
                <InputLabel id="student-ethinic-origin-label">
                  Student's Ethinic Origin
                </InputLabel>
                <Select
                  labelId="student-ethinic-origin-label"
                  id="student-ethinic-origin"
                  value={ethenicOrigin}
                  label="StudentsEthinicOrigin"
                  onChange={handleEthincOrigin}
                  sx={{ mb: 2 }}
                >
                  <MenuItem value={1}>Caucassian</MenuItem>
                  <MenuItem value={2}>African American</MenuItem>
                  <MenuItem value={3}>Hispanic</MenuItem>
                  <MenuItem value={4}>Two Or More Races</MenuItem>
                  <MenuItem value={5}>Unknown</MenuItem>
                  <MenuItem value={6}>Non Resident Alien/International</MenuItem>
                  <MenuItem value={7}>Asian</MenuItem>
                  <MenuItem value={8}>American Indian/Alaska Native</MenuItem>
                  <MenuItem value={9}>Native Hawaiian/Pacific Islander</MenuItem>
                </Select>
              </FormControl>
            </MDBCol>
          </MDBRow>
          <MDBRow>
            <MDBCol md="12">
              <FormControl>
                <FormLabel id="gender-label">Gender</FormLabel>
                <RadioGroup
                  row
                  aria-labelledby="gender-label"
                  name="row-radio-buttons-group"
                  sx={{ mb: 2 }}
                >
                  <FormControlLabel
                    value="female"
                    control={<Radio />}
                    label="Female"
                  />
                  <FormControlLabel
                    value="male"
                    control={<Radio />}
                    label="Male"
                  />
                  <FormControlLabel
                    value="Not binary"
                    control={<Radio />}
                    label="Not Binary"
                  />
                  <FormControlLabel
                    value="Prefer Not to say"
                    control={<Radio />}
                    label="Prefer Not to Say"
                  />
                </RadioGroup>
              </FormControl>
            </MDBCol>
          </MDBRow>
        </MDBCardBody>
      </MDBCard>
    </div>
  );
}

export default DemographicInformation;
