import React, { useState } from "react";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBCard,
  MDBCardBody,
} from "mdb-react-ui-kit";
import {
  Checkbox,
  FormControlLabel,
  FormLabel,
  FormGroup,
  Radio,
  RadioGroup,
  Slider,
  Switch,
  TextField,
  Tooltip,
  IconButton,
  Grid,
} from "@mui/material";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import Autocomplete from "@mui/material/Autocomplete";
import Stack from "@mui/material/Stack";
import GeoprahicPreference from "./GeopraphicPreference/GeoprahicPreference";
import InfoIcon from "@mui/icons-material/Info";

import FOS from './fos';


function PreferenceMotivation() {
  const [age, setAge] = React.useState("");
  const [satScore, setSatScore] = useState(false);
  const [affinity, setAffinity] = useState(false);
  const [affordability, setAffordability] = useState(false);
  const [addmisionability, setAddmissionability] = useState(false);
  const [religious_affiliation, setreligious_affiliation] = React.useState("");
  const [specialized_mission, setspecialized_mission] = React.useState("");

  const religious_affiliationchange = (event) => {
    setreligious_affiliation(event.target.value);
  };
  const specialized_missionchange = (event) => {
    setspecialized_mission(event.target.value);
  };

  const affinityPercentShow = () => {
    if (affinity == false) {
      document.getElementById("affinity-percent").style.display = "block";
      setAffinity(true);
    } else {
      document.getElementById("affinity-percent").style.display = "none";
      setAffinity(false);
    }
  };
  const affordalityPercentShow = () => {
    if (affordability == false) {
      document.getElementById("affordability-percent").style.display = "block";
      setAffordability(true);
    } else {
      document.getElementById("affordability-percent").style.display = "none";
      setAffordability(false);
    }
  };
  const addmisabilityPercentShow = () => {
    if (addmisionability == false) {
      document.getElementById("addmisionability-percent").style.display =
        "block";
      setAddmissionability(true);
    } else {
      document.getElementById("addmisionability-percent").style.display =
        "none";
      setAddmissionability(false);
    }
  };

  const handleResidencyStatus = (event) => {
    if (event.target.value === 2) {
      document.getElementById("citenship_status").style.display = "block";
    } else {
      document.getElementById("citenship_status").style.display = "none";
    }
  };

  const college = [
    { title: "Public" },
    { title: "Private Non-Profit" },
    { title: "Private Profit" },
  ];

  const fields = [
    { title: "Engineering Mechanics" },
    { title: "Electro Mechanical Engineering" },
    { title: "Liberal And Arts" },
  ];

  const displayItem = () => {
    var sat = document.getElementById("psat_score_enable");

    console.log("asfd");
  };

  return (
    <div>
      <MDBCard>
        <MDBCardBody className="px-4">
          <h3 className="fw-bold mb-4 pb-2 pb-md-0 mb-md-5">
            Preferences And Motivations
          </h3>
          <MDBRow>
            <Stack spacing={3}>
              <Autocomplete
                multiple
                id="preferences-and-motivations"
                options={college}
                getOptionLabel={(option) => option.title}
                filterSelectedOptions
                renderInput={(params) => (
                  <TextField {...params} label="College Type" />
                )}
              />
            </Stack>
          </MDBRow>
          <br />
          <MDBRow>
            <Stack spacing={3}>
              <Autocomplete
                id="field-of-study"
                options={FOS.Fields}
                getOptionLabel={(option) => option.FOS}
                filterSelectedOptions
                renderInput={(params) => (
                  <TextField
                    {...params}
                    label="Field of Study"
                    placeholder="Field of Study"
                  />
                )}
              />
            </Stack>
          </MDBRow>
          <br />
          <MDBRow>
            <MDBCol md='12'>
              <GeoprahicPreference />
            </MDBCol>
          </MDBRow>
          <br />
          <MDBRow>
            <MDBCol md='12'>
            <FormControl fullWidth>
                <InputLabel id="religious-affiliation-select-label">
                  Religious Affliations
                </InputLabel>
                <Select
                  labelId="religious-affiliation-select-label"
                  id="religious-affiliation-select"
                  value={religious_affiliation}
                  label="Religious Affliations"
                  onChange={religious_affiliationchange}
                  fullWidth
                >
                  <MenuItem value={10}>Very important</MenuItem>
                  <MenuItem value={10}>Somewhat important</MenuItem>
                  <MenuItem value={10}>Not at all important</MenuItem>
                </Select>
              </FormControl>
            </MDBCol>
          </MDBRow>
          <br/>
          <MDBRow>
            <MDBCol md='12'>
            <FormControl fullWidth>
                <InputLabel id="specialized-mission-select-label">
                  Specialized Mission
                </InputLabel>
                <Select
                  labelId="specialized-mission-select-label"
                  id="specialized-mission-select"
                  value={specialized_mission}
                  label="Specialized Mission"
                  onChange={specialized_missionchange}
                >
                  <MenuItem value={10}>Very Important</MenuItem>
                  <MenuItem value={10}>Somewhat Important</MenuItem>
                  <MenuItem value={10}>Not at all Important</MenuItem>
                </Select>
              </FormControl>
            </MDBCol>
          </MDBRow>
          <br/>
          <MDBRow>
            <MDBCol md='12'>
            <FormControl component="fieldset">
              <FormLabel component="legend">School Size</FormLabel>
              <FormGroup aria-label="position" row>
                <FormControlLabel
                  value="Affinity - perceived level of fit with the school"
                  control={<Checkbox />}
                  label="Small"
                  labelPlacement="end"
                />
                <FormControlLabel
                  value="Affordability - whether I/we can afford it"
                  control={<Checkbox />}
                  label="Medium"
                  labelPlacement="end"
                />
                <FormControlLabel
                  value="Admissibility and academics - reputation and my chances of getting in"
                  control={<Checkbox />}
                  label="Large"
                  labelPlacement="end"
                />
              </FormGroup>
            </FormControl>
            </MDBCol>
          </MDBRow>
          <MDBRow>
            <MDBCol md="12">
              <FormControl component="fieldset">
                <FormLabel component="legend">Urbanicity</FormLabel>
                <FormGroup aria-label="position" row>
                  <FormControlLabel
                    control={<Checkbox />}
                    label="City"
                    labelPlacement="end"
                  />
                  <FormControlLabel
                    control={<Checkbox />}
                    label="Suburban"
                    labelPlacement="end"
                  />
                  <FormControlLabel
                    control={<Checkbox />}
                    label="Town"
                    labelPlacement="end"
                  />
                  <FormControlLabel
                    control={<Checkbox />}
                    label="Rural"
                    labelPlacement="end"
                  />
                </FormGroup>
              </FormControl>
            </MDBCol>
          </MDBRow>
          <br />
          <MDBRow>
            <MDBCol md="6">
              <FormControl component="fieldset">
                <FormLabel component="legend">
                  Your reasons to attend college?
                </FormLabel>
                <FormGroup aria-label="position">
                  <FormControlLabel
                    value="Good job/maximize income upon graduation"
                    control={<Checkbox />}
                    label="Obtain a good job upon graduation"
                    labelPlacement="end"
                  />
                  <FormControlLabel
                    value="Kinship"
                    control={<Checkbox />}
                    label="Kinship"
                    labelPlacement="end"
                  />
                  <FormControlLabel
                    value="Pursue post-graduate education"
                    control={<Checkbox />}
                    label="Pursue post-graduate education"
                    labelPlacement="end"
                  />
                  <div style={{ display: "none" }}>
                    <MDBCol md="8" id="other">
                      <TextField
                        id="residential-status-non-us-text-field"
                        label="Reason?"
                        variant="standard"
                        fullWidth
                      />
                    </MDBCol>
                  </div>
                </FormGroup>
              </FormControl>
            </MDBCol>
            <MDBCol md="6">
              <FormControl component="fieldset">
                <FormLabel component="legend">
                  Considerations when selecting a college and relative
                  importance?
                </FormLabel>
                <FormGroup aria-label="position">
                  <Grid direction="row">
                    <FormControlLabel
                      value="Affinity"
                      control={<Checkbox onChange={affinityPercentShow} />}
                      label="Affinity"
                      labelPlacement="end"
                    />
                    <Tooltip title="Takes into account factors such as college's reputation, ranking, campus safety, transporation access, weather, and perceived cultural fit">
                      <IconButton>
                        <InfoIcon />
                      </IconButton>
                    </Tooltip>
                  </Grid>
                  <Grid direction="row">
                    <FormControlLabel
                      value="Affordability"
                      control={<Checkbox onChange={affinityPercentShow} />}
                      label="Affordability"
                      labelPlacement="end"
                    />
                    <Tooltip title="Whether I/we can afford it ">
                      <IconButton>
                        <InfoIcon />
                      </IconButton>
                    </Tooltip>
                  </Grid>
                  <Grid direction="row">
                    <FormControlLabel
                      value="Affinity"
                      control={<Checkbox onChange={affinityPercentShow} />}
                      label="Admissibility"
                      labelPlacement="end"
                    />
                    <Tooltip title="Student's chances of getting admitted ">
                      <IconButton>
                        <InfoIcon />
                      </IconButton>
                    </Tooltip>
                  </Grid>
                </FormGroup>
              </FormControl>
            </MDBCol>
          </MDBRow>
        </MDBCardBody>
      </MDBCard>
    </div>
  );
}
export default PreferenceMotivation;
