import React, { useEffect } from "react";
import { MDBRow, MDBCol, MDBCard, MDBCardBody } from "mdb-react-ui-kit";
import {
  Checkbox,
  FormControl,
  FormControlLabel,
  InputLabel,
  Select,
  MenuItem,
  Slider,
  Switch,
  TextField,
  Stack,
  Autocomplete,
} from "@mui/material";

import Schools from "./Schools";

function AcademicProfile() {
  const [age, setAge] = React.useState("");
  const [transferyear, setTransferYear] = React.useState(true);
  const [typeofdegree, setTypeOfDegree] = React.useState(true);
  const [enroll, setEnroll] = React.useState("");
  const [student_percentile, setStudentPercentile] = React.useState("");
  const [gpa_type, setGpaType] = React.useState("");
  const [psat_enable, setPsatEnable] = React.useState(false);
  const [sat_enable, setsatEnable] = React.useState(false);
  const [act_enable, setactEnable] = React.useState(false);
  const [psat_math, setpsatMath] = React.useState(0);
  const [psat_verbal, setpsatVerbal] = React.useState(0);
  const [sat_math, setSatMath] = React.useState(0);
  const [sat_verbal, setSatVerbal] = React.useState(0);
  const [act_composite, setActComposite] = React.useState(0);


  const type_of_degreechange = (event) => {
    setTypeOfDegree(event.target.value);
  };

  const enrollchange = (event) => {
    setEnroll(event.target.value);
    if (event.target.value == 1) {
      document.getElementById("transfer_as").style.display = "none";
    } else {
      document.getElementById("transfer_as").style.display = "block";
    }
  };

  const student_percentilechange = (event) => {
    setStudentPercentile(event.target.value);
  };
  const gpa_typechange = (event) => {
    setGpaType(event.target.value);
  };

  const transferaschange = (event) => {
    setTransferYear(event.target.value);
  };

  const enablePsat = () => {
    if (psat_enable === true) {
      setPsatEnable(false);
    } else {
      setPsatEnable(true);
    }
  };
  const enablesat = () => {
    if (sat_enable === true) {
      setsatEnable(false);
    } else {
      setsatEnable(true);
    }
  };
  const enableact = () => {
    if (act_enable === true) {
      setactEnable(false);
    } else {
      setactEnable(true);
    }
  };

  const graduation_years = [];
  for (let index = 0; index < 7; index++) {
    graduation_years.push(new Date().getFullYear() + index);
  }

  useEffect(() => {
    if (psat_enable === false) {
      document.getElementById("psat-score-math-visible").style.display = "none";
      document.getElementById("psat-score-verbal-visible").style.display =
        "none";
    }
    if (psat_enable === true) {
      document.getElementById("psat-score-math-visible").style.display =
        "block";
      document.getElementById("psat-score-verbal-visible").style.display =
        "block";
    }
    if (sat_enable === false) {
      document.getElementById("sat-score-math-visible").style.display = "none";
      document.getElementById("sat-score-verbal-visible").style.display =
        "none";
    }
    if (sat_enable === true) {
      document.getElementById("sat-score-math-visible").style.display = "block";
      document.getElementById("sat-score-verbal-visible").style.display =
        "block";
    }
    if (act_enable === false) {
      document.getElementById("act-score-composite-visible").style.display =
        "none";
    }
    if (act_enable === true) {
      document.getElementById("act-score-composite-visible").style.display =
        "block";
    }
  });

  return (
    <div>
      <MDBCard>
        <MDBCardBody className="px-4">
          <h3 className="fw-bold mb-4 pb-2 pb-md-0 mb-md-5">
            Academic Profile
          </h3>
          <MDBRow>
            <MDBCol md="12">
              <Autocomplete
                id="schools-highschools"
                options={Schools.HighSchools}
                getOptionLabel={(option) => option.NAME}
                filterSelectedOptions
                sx={{ mb: 2 }}
                renderInput={(params) => (
                  <TextField
                    {...params}
                    label="Name of High School"
                    placeholder="Name of High School"
                  />
                )}
              />
            </MDBCol>
          </MDBRow>
          <MDBRow>
            <MDBCol md="6">
              <FormControl fullWidth>
                <InputLabel id="high-school-graduation-completion-year-select-label">
                  High School Graduation/Completion Year
                </InputLabel>
                <Select
                  labelId="high-school-graduation-completion-year-select-label"
                  id="high-school-graduation-completion-year-select"
                  label="High School Graduation/completion year "
                  sx={{ mb: 2 }}
                >
                  {graduation_years.map((year) => (
                    <MenuItem value={year}>{year}</MenuItem>
                  ))}
                </Select>
              </FormControl>
            </MDBCol>
            <MDBCol md="6">
              <FormControl fullWidth>
                <InputLabel id="looking-to-enroll-in-select-label">
                  Looking to Enroll in
                </InputLabel>
                <Select
                  labelId="looking-to-enroll-in-label"
                  id="looking-to-enroll-in-select"
                  label="Looking To Enroll In"
                  sx={{ mb: 2 }}
                >
                  <MenuItem value={1}>
                    Spring {new Date().getFullYear() + 1}
                  </MenuItem>
                  <MenuItem value={2}>
                    Fall {new Date().getFullYear() + 1}
                  </MenuItem>
                  <MenuItem value={3}>
                    Spring {new Date().getFullYear() + 2}
                  </MenuItem>
                  <MenuItem value={4}>
                    Fall {new Date().getFullYear() + 2}
                  </MenuItem>
                </Select>
              </FormControl>
            </MDBCol>
          </MDBRow>
          <MDBRow>
            <MDBCol md="6">
              <FormControl fullWidth>
                <InputLabel id="enrolled-as-select-label">Enrol as</InputLabel>
                <Select
                  labelId="enrolled-as-select-label"
                  id="enrolled-as-select"
                  value={enroll}
                  label="ENROLLED AS"
                  sx={{ mb: 2 }}
                  onChange={enrollchange}
                >
                  <MenuItem value={1}>First Year</MenuItem>
                  <MenuItem value={2}>Transfer</MenuItem>
                </Select>
              </FormControl>
            </MDBCol>
            <MDBCol md="6">
              <FormControl fullWidth>
                <InputLabel id="type-of-degree-label">
                  Type of Degree
                </InputLabel>
                <Select
                  labelId="type-of-degree-label"
                  id="type-of-degree"
                  value={typeofdegree}
                  sx={{ mb: 2 }}
                  onChange={type_of_degreechange}
                  label="Type of Degree"
                >
                  <MenuItem value={1}>Certificate</MenuItem>
                  <MenuItem value={2}>Associate Degree</MenuItem>
                  <MenuItem value={3}>Bachelor's Degree</MenuItem>
                </Select>
              </FormControl>
            </MDBCol>
          </MDBRow>
          <MDBRow>
            <MDBCol md="6" id="transfer_as" style={{ display: "none" }}>
              {[typeofdegree].map((res) =>
                res == 3 ? (
                  <div>
                    <FormControl fullWidth>
                      <InputLabel id="transfer-year-select-label">
                        Transfer as
                      </InputLabel>
                      <Select
                        labelId="transfer-year-select-label"
                        id="transfer-year-select"
                        value={transferyear}
                        label="Transfer as"
                        sx={{ mb: 2 }}
                        onChange={transferaschange}
                      >
                        <MenuItem value={1}>Sophmore</MenuItem>
                        <MenuItem value={2}>Junior</MenuItem>
                        <MenuItem value={3}>Senior</MenuItem>
                      </Select>
                    </FormControl>
                  </div>
                ) : (
                  <div>
                    <FormControl fullWidth>
                      <InputLabel id="transfer-year-select-label">
                        Transfer as
                      </InputLabel>
                      <Select
                        labelId="transfer-year-select-label"
                        id="transfer-year-select"
                        value={transferyear}
                        label="Transfer as"
                        sx={{ mb: 2 }}
                        onChange={transferaschange}
                      >
                        <MenuItem value={3}>Senior</MenuItem>
                      </Select>
                    </FormControl>
                  </div>
                )
              )}
            </MDBCol>
            <MDBCol md="6">
              <FormControl fullWidth>
                <InputLabel id="student-class-rank-select-label">
                  Student Class Rank (Percentile)
                </InputLabel>
                <Select
                  labelId="student-class-rank-select-label"
                  id="student-class-rank-select"
                  value={student_percentile}
                  label="Student Class Rank (percentile)"
                  sx={{ mb: 2 }}
                  onChange={student_percentilechange}
                >
                  <MenuItem value={1}>Top 10%</MenuItem>
                  <MenuItem value={2}>Top Quarter</MenuItem>
                  <MenuItem value={3}>Top Half</MenuItem>
                  <MenuItem value={4}>Bottom Half</MenuItem>
                  <MenuItem value={5}>Bottom Quarter</MenuItem>
                </Select>
              </FormControl>
            </MDBCol>
          </MDBRow>
          <MDBRow>
            <MDBCol md="6">
              <FormControl fullWidth>
                <InputLabel id="gpa-type-label">GPA Type</InputLabel>
                <Select
                  labelId="gpa-type-label"
                  id="gpa-type"
                  value={gpa_type}
                  label="GPA Type"
                  sx={{ mb: 2 }}
                  onChange={gpa_typechange}
                >
                  <MenuItem value={1}>Weighted</MenuItem>
                  <MenuItem value={2}>Unweighted</MenuItem>
                </Select>
              </FormControl>
            </MDBCol>
            <MDBCol md="6">
              <MDBRow>
                <MDBCol md="5">
                  <TextField
                    id="your-gpa-standard-basic"
                    type="number"
                    label="Obtained GPA"
                    variant="standard"
                    sx={{ mb: 2 }}
                    fullWidth
                  />
                </MDBCol>
                <MDBCol md="1">
                  <center>
                    <p style={{ fontSize: "2vw" }}>/</p>
                  </center>
                </MDBCol>
                <MDBCol md="5">
                  <TextField
                    id="toatal-gpa-standard-basic"
                    type="number"
                    label="Total GPA"
                    variant="standard"
                    sx={{ mb: 2 }}
                    fullWidth
                  />
                </MDBCol>
              </MDBRow>
            </MDBCol>
          </MDBRow>
          <br />
          <MDBRow>
            <MDBCol md="6">
              <FormControl fullWidth>
                <p>
                  International Baccalauerate (IB)
                  <Switch labelId="international-baccalauerate-label" sx={{ mb: 2 }}/>
                </p>
              </FormControl>
            </MDBCol>
            <MDBCol md="6">
              <TextField
                id="predicted-ib-score"
                type="number"
                label="Predicted IB Score (Out of 45)"
                variant="standard"
                sx={{ mb: 2 }}
                fullWidth
              />
            </MDBCol>
          </MDBRow>
          <MDBRow>
            <MDBCol md="3">
              <FormControlLabel
                control={<Checkbox onChange={enablePsat} />}
                label="PSAT Score "
                labelPlacement="end"
                id="psat-score-enable"
                sx={{ mb: 2 }}
              />
            </MDBCol>
            <MDBCol md="4" id="psat-score-math-visible">
              <TextField
                id="psat-score-math"
                type="number"
                label="PSAT MATH"
                variant="standard"
                sx={{ mb: 2 }}
                fullWidth
              />
            </MDBCol>
            <MDBCol md="4" id="psat-score-verbal-visible">
              <TextField
                id="psat-score-verbal"
                type="number"
                label="PSAT READING"
                variant="standard"
                sx={{ mb: 2 }}
                fullWidth
              />
            </MDBCol>
          </MDBRow>
          <MDBRow>
            <MDBCol md="3">
              <FormControlLabel
                control={<Checkbox onChange={enablesat} />}
                label="SAT Score "
                labelPlacement="end"
                id="sat-score-enable"
                sx={{ mb: 2 }}
              />
            </MDBCol>
            <MDBCol md="4" id="sat-score-math-visible">
              <TextField
                id="sat-score-math"
                type="number"
                label="SAT MATH"
                variant="standard"
                sx={{ mb: 2 }}
                fullWidth
              />
            </MDBCol>
            <MDBCol md="4" id="sat-score-verbal-visible">
              <TextField
                id="sat-score-verbal"
                type="number"
                label="SAT READING"
                variant="standard"
                sx={{ mb: 2 }}
                fullWidth
              />
            </MDBCol>
          </MDBRow>
          <MDBRow>
            <MDBCol md="3">
              <FormControlLabel
                control={<Checkbox onChange={enableact} />}
                label="ACT Score "
                labelPlacement="end"
                sx={{ mb: 2 }}
                id="act-score-enable"
              />
            </MDBCol>
            <MDBCol md="4" id="act-score-composite-visible">
              <TextField
                id="act-score-composite"
                type="number"
                label="ACT Composite"
                variant="standard"
                sx={{ mb: 2 }}
                fullWidth
              />
            </MDBCol>
           
          </MDBRow>
        </MDBCardBody>
      </MDBCard>
    </div>
  );
}
export default AcademicProfile;
